import React, { Component } from 'react';
import './css/style1.css';
import './css/css.css';
import './css/home.css';
import './css/xinfugai.css';
import biao from './images/biao.png';
import zhongji from './images/zhongji.png';
import shouxian from './images/shouxian.png';
import yiliao from './images/yiliao.png';
import yiwai from './images/yiwai.png';
import qita from './images/qita.png';

export default class PlanList extends Component {
  render() {
    const typeMappings = [zhongji,shouxian,yiliao,yiwai,qita];
    const items = []
    this.props.data.forEach(value => {
      items.push(
        <div className="novice_zs_f_in" style={{ marginBottom: '6px' }}>
          <div className="novice_zs_f_in_l">
            <div className="novice_zs_f_in_l_top">
              <img src={typeMappings[value.type]} />
              <span>{value.title}</span>
            </div>
            <div className="novice_zs_f_in_l_bottom">
              <ul>
                <li className="mark1_f">
                  <em>{value.range}</em>
                  <p>保险期间</p>
                </li>
                <li className="mark2_f">
                  <em>{value.paymentMethod}</em>
                  <p>缴费方式</p>
                </li>
                <li className="mark3_f">
                  <em>{value.sumInsured}</em>
                  <p>保额</p>
                </li>
                <li className="mark4_f">
                  <em><strong>{`${value.price}/年`}</strong></em>
                  <p>费用</p>
                </li>
              </ul>
              <ul>
                <li className="mark5_f">
                  <p>附加说明</p>
                </li>
                <li className="mark6_f">
                  <p>{value.extraInfo}</p>
                </li>
              </ul>
            </div>
          </div>
          <a href={value.link}><div className="novice_zs_f_in_r">
            <span>立即投保</span>
          </div></a>
        </div>);
    });

    return (
      <div>
        {items}
      </div>
    )
  }
}
