import React from 'react';
import './App.css';
import './css/style1.css';
import './css/css.css';
import './css/home.css';
import './css/xinfugai.css';
import PlanList from './PlanList';
import Plans from './PlanData';
import plansImg from './images/plans.png';
import QueryString from 'query-string';
import logo from './images/logo.png';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { index: 0 };
    const params = QueryString.parse(window.location.search);
    const key = params.k;
    this.Plan = Plans.find(elemnet => elemnet.key === key);
    this.Sum = 0;
    if(this.Plan){
      this.Plan.plan.forEach(item => item.products.forEach(product => this.Sum = this.Sum + product.price));
    }
  }

  handleClick = (i) => {
    this.setState({ index: i });
  }

  render() {
    if(!this.Plan) return(<div>no contents</div>);
    return ( 
      <div className="App">
        <div style={{ display: 'none' }}><img src={plansImg} /></div>
        <div className="novice_zs_f_in_main" style={{ marginBottom: '6px' }}>
          <img src={logo} />
          <span>{`总费用：约${this.Sum}/年`}</span>
        </div>
        <div className="sizdzh_f">
          <ul>
            {this.Plan.plan.map((value, index) => {
              return <li key={index} className={index === this.state.index ? 'addys_f' : ''} onClick={() => this.handleClick(index)}>{value.name}</li>
            })}
          </ul>
        </div>
        <div className="display_f">
          <div className="display_f_in">
            <div className="fourcp_1">
              <PlanList data={this.Plan.plan[this.state.index].products} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
